import React, { Component } from 'react';
import './feedback.css';
import firebase from './firebase';
class Feedback extends Component{
    constructor(props){
        super(props);
        this.state = {
                name: '',
                feedback: '',
                isSubmit: false,
                email:''
        }
        this.setemail = this.setemail.bind(this);
        this.setfeedback = this.setfeedback.bind(this);
        this.setname = this.setname.bind(this);
        this.save = this.save.bind(this);

    }
    setemail(e){
        this.setState({
            email: e.target.value
        })
    }
    setname(e){
        this.setState({  
            name: e.target.value
        })
    }
    setfeedback(e){
        this.setState({
            feedback: e.target.value
        })
    }
    save(e){
        firebase.database().ref('FeedBacks').orderByKey().limitToLast(100);
        firebase.database().ref('FeedBacks').push(this.state);
        this.setState({
            name:'',
            feedback:'',
            isSubmit: !this.state.isSubmit
        })
    }


    render(){
        return (
            <div>
                <div id="form" className="feedback"> 
                <br></br>
                <br></br>
                <br></br>
                <br></br>
                {this.state.isSubmit === false 
                ?
                <form onSubmit={this.save} className="col-sm-12">
                <div className="form-group">
                    <label htmlFor="name">Name</label>
                    <input type="text" value={this.state.name} onChange={this.setname} className="form-control" id="name" placeholder="Enter Your Name" />
                </div>
                <div className="form-group">
                    <label htmlFor="Email" >E-mail</label>
                    <input onChange={this.setemail} style={{minHeight:"50px"}} value={this.state.email} type="email" className="form-control" id="email" placeholder="E-Mail" />
                </div>
                <div className="form-group">
                    <label htmlFor="feedback" >FeedBack</label>
                    <textarea minLength="10" onChange={this.setfeedback} style={{minHeight:"50px"}} value={this.state.feedback} type="text" className="form-control" id="feedback" placeholder="feedback" />
                </div>

                <br></br>
                <center>
                <input className="btn btn-success" type="submit" id="submit" className="btn btn-primary"></input>
                </center>
                </form> 
                :
                <p>Thanks for your valuable feedback</p>
    }
                <br></br>
                <br></br>
                
                </div>
                <footer><i className="fa fa-copyright"></i>This site is built and owned by Siva Vignesh Siva Kumar</footer>

            </div>
                
   
        )
    }

}
export default Feedback;