import React, { Component } from 'react';
import './contacts.css';

class Contact extends Component{
render(){
    return(
        <div id='contact'>
            <br></br>
            <div className="row" id="firstdiv" style={{marginLeft:"250px"}}>
                <button className="col-md-4 col-sm-12"><a href="#" style={{fontSize:"30px"}}>Github   </a><i className="fab fa-github" style={{fontSize:"45px"}}></i></button>
                <button className="col-md-4 col-sm-12" style={{marginLeft:"100px",fontSize:"30px"}}><a href="#">Linkedin   </a><i className="fab fa-linkedin" style={{fontSize:"45px"}}></i></button>
            </div>
            <br></br>
            <div className="row" id="seconddiv" style={{marginLeft:"250px"}}>
                <button className="col-md-4 col-sm-12"><a href="#" style={{fontSize:"30px"}}>Instagram    </a><i className="fab fa-instagram" style={{fontSize:"45px"}}></i></button>
                <button className="col-md-4 col-sm-12" style={{marginLeft:"100px",fontSize:"30px"}}><a href="#" style={{fontSize:"30px"}}>Facebook    </a><i className="fab fa-facebook" style={{fontSize:"45px"}}></i></button>
            </div>
            <br></br>
            <div className="row" id="thirddiv" style={{marginLeft:"250px"}}>
                <button className="col-md-4 col-sm-12"><a href="#" style={{fontSize:"30px"}}>Phone    </a><i className="fa fa-phone" style={{fontSize:"45px"}}></i></button>
                <button className="col-md-4 col-sm-12" style={{marginLeft:"100px",fontSize:"30px"}}><a href="#" style={{fontSize:"30px"}}>G-Mail    </a><i className="fa fa-envelope" style={{fontSize:"45px"}}></i></button>
            </div>



            <footer><i className="fa fa-copyright"></i>This site is built and owned by Siva Vignesh Siva Kumar</footer>
        </div>
    );
}


}
export default Contact;