import React,{ Component } from 'react';
import firebase from './firebase';
import './Disfeed.css';
import { Card,CardBody,CardFooter,CardTitle } from 'reactstrap';
class Displayfeed extends Component {

constructor(props) {
    super(props);
    this.state = 
    {
        Feed : [],
        count: 0
    }

    
}
  componentDidMount() {
      firebase.database().ref("FeedBacks").on("value", snapshot => {
        let Feeds = [];
        snapshot.forEach(snap => {
            Feeds.push(snap.val());
        });
        if ((Feeds.length) >= 1 ){
            this.setState({
                count: 1
            })
        }
        this.setState({
             Feed: Feeds,
            });
      });
    
    
 }


  render(){
  return (
      <div id="Bord">
          <h1 style={{textAlign:"center",fontFamily:"fantasy"}} id="h1">FeedBacks</h1>
          {this.state.count === 0
          ?
          <div>No feedbacks yet...</div>
          :
          <div>
            {this.state.Feed.map(data => {
                return (
                    <div id="Card">
                        <Card className="bg-success">
                            <CardTitle style={{fontFamily:"cursive",fontSize:"30px"}}>{data.name}</CardTitle>
                            <CardBody>{data.feedback}</CardBody>
                            <CardFooter>Author : Thankyou {data.name} <i className="fa fa-thumbs-up" ></i> </CardFooter>
                        </Card>
                    </div>
                );
                })}
            </div>
  }

     </div>
  );
}
}
export default Displayfeed;