import React, { Component } from 'react';
import './skills.css';

class skills extends Component {
    render(){
        return (
            
            <div className="container" id="skills">
            <br></br>
            <div className="skill1">
                <h1>Python  <i className="fab fa-python"></i></h1>
                Skill Level
                <div className="progress" style={{width:"50%",marginLeft:"250px",border:"2px solid gray"}}>
                <div className="progress-bar bg-success" style={{width:"80%" }}></div>
                </div>
                <br></br>
            </div>
            <div className="skill2">
                <h1>JavaScript  <i className="fab fa-js"></i></h1>
            Skill Level
                <div className="progress" style={{width:"50%",marginLeft:"250px",border:"2px solid gray"}}>
                <div className="progress-bar bg-success" style={{width:"80%" }}></div>
                </div>
                <br></br>

            </div>

            <div className="skill3">
            <h1>React JS  <i className="fab fa-react"></i></h1>
            Skill Level
                <div className="progress" style={{width:"50%",marginLeft:"250px",border:"2px solid gray"}}>
                <div className="progress-bar bg-success" style={{width:"70%" }}></div>
                </div>
                <br></br>
            </div>
            <div className="skill3">
            <h1>HTML  <i className="fab fa-html5"></i></h1>
            Skill Level
                <div className="progress" style={{width:"50%",marginLeft:"250px",border:"2px solid gray"}}>
                <div className="progress-bar bg-success" style={{width:"85%" }}></div>
                </div>
                <br></br>
            </div>
            <div className="skill3">
            <h1>CSS3   <i className="fab fa-css3"></i></h1>
            Skill Level
                <div className="progress" style={{width:"50%",marginLeft:"250px",border:"2px solid gray"}}>
                <div className="progress-bar bg-success" style={{width:"80%" }}></div>
                </div>
                <br></br>
            </div>
            <div className="skill3">
            <h1>C++  <i className="fab fa-c++"></i></h1>
            Skill Level
                <div className="progress" style={{width:"50%",marginLeft:"250px",border:"2px solid gray"}}>
                <div className="progress-bar bg-success" style={{width:"70%" }}></div>
                </div>
                <br></br>
            </div>
            <div className="skill3">
            <h1>React-Native    <i className="fab fa-react"></i></h1>
            Skill Level
                <div className="progress" style={{width:"50%",marginLeft:"250px",border:"2px solid gray"}}>
                <div className="progress-bar bg-success" style={{width:"10%" }}></div>
                </div>
                <br></br>
            </div>
            </div>
        )
    }

}

export default skills;
